@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;900&display=swap);
body{margin:0;padding:0;box-sizing:border-box}#root{width:100vw;height:100vh;display:flex}#root .left{width:20%;height:100%;background:#070707;color:white;display:flex;flex-direction:column;align-items:center;overflow-y:scroll;position:relative;scrollbar-color:#323232 rgba(0,0,0,0);scrollbar-width:thin}#root .left::-webkit-scrollbar{width:0.5vw;height:auto}#root .left::-webkit-scrollbar-thumb{background:#323232;border-radius:0.1vw}#root .left::-webkit-scrollbar-thumb:hover{background:#3b3b3b;box-shadow:0 0 2px 1px rgba(0,0,0,0.2)}#root .left::-webkit-scrollbar-thumb:active{background-color:#292929}#root .left .logo{width:80%;margin-top:3vw;margin-bottom:3vw;transition:0.1s}#root .left .logo img{width:100%}#root .left .logo:hover{transform:scale(1.1);transition:0.1s}#root .left .h3-wifi{width:80%;margin:0;font-family:Calibri;font-weight:300;font-size:2vw}#root .left hr{background:#2b2b2b;width:80%;height:0.05vw;border:none;margin-top:0;margin-bottom:0.5vw}#root .left .data-wifi{width:80%;height:3vw;background:#141414;display:flex;flex-direction:row;align-items:center;margin-bottom:0.5em;font-family:Calibri;border-radius:0.2vw}#root .left .data-wifi .png-container{width:20%;height:100%;display:flex;justify-content:center;align-items:center;font-size:1.4vw}#root .left .data-wifi p{width:80%;height:100%;font-size:0.8em;display:flex;justify-content:felx-start;align-items:center;font-size:1.4vw;font-weight:300}#root .left .font-color{background:none}#root .left .font-color .png-container{width:10%}#root .left .font-color .png-container i{color:red;font-size:1.1vw}#root .left .font-color p{color:red;font-size:0.8vw}#root .left .menu-container{width:80%;height:auto;margin-top:1vw;display:flex;flex-direction:column;justify-content:center;align-items:center}#root .left .menu-container .data-menu{width:100%;height:2.5vw;display:flex;flex-direction:row;align-items:center;margin:0;font-family:Calibri;color:#4e4e4e;text-decoration:none}#root .left .menu-container .data-menu .png-menu-container{width:20%;height:100%;display:flex;justify-content:center;align-items:center;font-size:1.2vw}#root .left .menu-container .data-menu p{width:80%;height:100%;display:flex;justify-content:felx-start;align-items:center;font-size:1.2vw;font-weight:300}#root .left .menu-container .data-menu:hover{background:#0f0f0f;color:white}#root .right{width:80%;height:100%;background:#E4E4E4;color:#0c0c0c;overflow-y:scroll}#root .right .slider{width:100%;height:40vw;display:flex;justify-content:center;align-items:center;background:black;position:relative;overflow:hidden}#root .right .slider .img-photos{animation:fade 0.3s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;width:100%;height:100%;position:absolute;top:0;left:0;object-fit:cover}@keyframes fade{0%{opacity:0}100%{opacity:1}}#root .right .slider i{position:absolute;width:10%;height:100%;color:white;-webkit-user-select:none;-ms-user-select:none;user-select:none;display:flex;justify-content:center;align-items:center;text-decoration:none;transition:all 0.1s ease;font-size:5vw;color:rgba(255,255,255,0.5);transition:all 0.1s ease;cursor:pointer}#root .right .slider .prev{left:0}#root .right .slider .next{right:0}#root .right .slider .active-arrow{background-color:rgba(0,0,0,0.3);transition:all 0.1s ease;color:white}#root .right .slider .dots-container{display:flex;justify-content:center;align-items:center;padding:0px;position:absolute;bottom:2vh}#root .right .slider .dots-container .dot{cursor:pointer;margin:0.5em;height:1.5vw;width:1.5vw;color:#333;border-radius:50%;background:rgba(255,255,255,0.1);transition:0.1s}#root .right .slider .dots-container .dot:hover{background:#fff;transition:0.1s}#root .right .slider .dots-container .dot.active{background:rgba(255,255,255,0.6)}#root .right .slider .dots-container .active{background:rgba(255,255,255,0.6)}#root .right .juegos-actualizados{width:100%;height:auto;overflow:hidden;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}#root .right .juegos-actualizados h2{width:40vw;height:5vw;display:flex;justify-content:center;align-items:center;color:white;box-shadow:1px 1px 10px 0px #646464;margin:1vw;font-family:'Roboto', sans-serif;font-size:3vw}#root .right .juegos-actualizados .juegos-color-h2{background:#ff006a}#root .right .juegos-actualizados .promos-color-h2{background:#0099ff}#root .right .juegos-actualizados span{width:100%;margin:1vw;font-family:'Roboto', sans-serif;font-weight:300;color:#000000;font-size:1.6vw}#root .right .games{width:100%;height:55vw;display:grid;justify-content:center;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(2, 1fr)}#root .right .games a{display:flex;justify-content:center;align-items:center}#root .right .games a .img-games{width:91%;height:91%;object-fit:cover;box-shadow:1px 1px 10px 0px #646464;transition:0.1s}#root .right .games a .img-games:hover{transform:scale(1.1);transition:0.1s}

